* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  margin: 0;
  padding: 0;
}

.cards-item {
  display: flex;
  align-items: center;
  margin: 5px;
}

li {
  list-style: none;
}

p {
  margin: 10px 0 0 0;
  font-weight: 600;
  font-size: 20px;
}

p span {
  font-weight: 400;
  font-size: 16px;
}

h3,
h2 {
  margin: 0;
}
img {
  margin-right: 10px;
}
